import React from 'react';
import {Route, Switch} from 'react-router-dom';
import asyncComponent from '../../../../../util/asyncComponent';

const Users = ({match}) => (
  <Switch>
      <Route exact from={`${match.url}/`} component={asyncComponent(() => import('./main.js'))}/>
      <Route exact from={`${match.url}/save`} component={asyncComponent(() => import('./UserSave.js'))}/>
      <Route exact from={`${match.url}/invite-user`} component={asyncComponent(() => import('./UsersInvite.js'))}/>
      <Route component={asyncComponent(() => import('app/routes/extraPages/routes/404'))}/>
  </Switch>
);

export default Users;